.theme_container {
  height: 100%;
  padding-bottom: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    height: 100%;
  }
  .in {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    h3 {
      width: 93%;
      font-family: Montserrat;
      font-style: normal;
      font-weight: 600;
      font-size: 1.9rem;
      text-align: center;
      background-color: rgb(218, 240, 255);
      margin: 0 30px;
      padding: 3px;
      border-radius: 10px;
      color: #0563fc;
    }

    button {
      margin-top: auto;
      margin-bottom: 10px;
      font-size: 1.2rem;
      padding: 15px;
      background-color: #5f4ef7;
      color: white;
      border-radius: 40px;
      border: none;
      transition: 0.2s ease-in-out;
      &:hover {
        transform: scale(1.1);
      }
    }

    p {
      margin: 20px;
      text-align: justify;
      font-family: Montserrat;
      font-style: normal;
      font-size: 1.3rem;
      padding: 0 10px;

      color: #001168;
    }
  }

  .ou {
    width: 100%;
    max-width: 1200px;
    display: flex;
    min-height: 325px;
    padding: 20px;
    height: 45vh;
    margin: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 7px 20px 0px rgb(223, 220, 220);
    border-radius: 20px;
    overflow: auto;
    transition: 0.4s ease-in-out;

    &:hover {
      transform: scale(1.05);
    }
  }

  h1 {
    margin-left: 2vw;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 1.9rem;
    text-shadow: 0px 10px 7px rgba(128, 128, 128, 0.534);
    color: #0563fc;
  }
  h2 {
    margin-left: 2vw;
    margin-bottom: 1vw;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 2.1rem;
    text-align:center;
    // text-shadow: 0px 10px 7px rgba(128, 128, 128, 0.836);
    color: #0563fc;
  }
}
.modal {
  height: 60vh;
  width: 70vw;
  margin: 20vh 15vw;
  z-index: 100;
  background: white;
  border: none;
  border-radius: 20px;
  padding: 10px;
  outline: none;
  h2 {
    font-family: Montserrat;
    color: #0563fc;
    text-align: center;
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 620px) {
  .theme_container {
    padding-bottom: 150px;
    h1 {
      font-size: 1.6rem;
    }
    h2 {
      font-size: 1.2rem;
    }

    img {
      width: 40%;
      height: auto;
      order: 2;
      // display: none;
    }

    .in {
      padding: 4px;
      p {
        padding: 4px;
        font-size: 0.8rem;
      }
      button {
        margin: auto;
        font-size: 0.8rem;
        padding: 12px;
      }
    }

    .ou {
      // overflow:visible;
      flex-direction: column;
      //   height:60vh;
      height: max-content;
      width: 84%;
      align-items: center;

      h3 {
        font-size: 1.5rem;
      }
    }
  }
}

@media screen and (max-height: 620px) {
  .theme_container {
       .ou {
    
    min-height: 325px;
    }
    .in {
      p {
        padding: 4px;
        font-size: 0.8rem;
      }
    }
  }
}
