.ham-menu {
  flex-grow: 12;
  // background-color: red;
  width: 100%;
  height: max-content;
  display: flex;
  background-color: white;
  z-index: 20000;
  border-radius: 0 0 20px 20px;
  box-shadow: 0px 7px 12px 0px rgba(230, 230, 230, 0.973);
  justify-content: space-around;
  align-items: center;
  padding:14px;

  .in-men {
    //   padding:20px 10px;
    a {
    //   padding: 20px 10px !important;
      font-size: 1.2rem;
      color: #0563fc;
      font-family: Montserrat;
      margin:10px;
      padding: 15px;

      &:hover{
         background-color: #0563fc;
         color:white;
         border-radius:10px;

      }
    }
    margin:10px;
  }
}
@media screen and (max-width: 620px) {

.ham-menu {
    flex-direction: column;
    box-shadow: 0px 7px 12px 0px rgba(201, 201, 201, 0.973);
}



}