//desktop
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");

.contest_container {
  height: 100%;
  padding-bottom: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .contest_title {
    width: max-content;
    margin-left: 3.8vw;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 2.6rem;
    text-shadow: 0px 10px 7px rgba(128, 128, 128, 0.836);
    color: #0563fc;
  }

  .card_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    flex-wrap: wrap;
  }

  .contest_card {
    position: relative;
    width: clamp(260px, 22vw, 320px);
    min-height: clamp(395px, 58vh, 450px);
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.05),
      inset -5px -5px 5px rgba(255, 255, 255, 0.5),
      5px 5px 5px rgba(0, 0, 0, 0.05), -5px -5px 5px rgba(255, 255, 255, 0.5);
    border-radius: 15px;
    margin: 30px;
    display: flex;
    align-items: center;
    justify-content: center;

    .contest_box {
      position: absolute;
      // top: 20px;
      // left: 20px;
      // right: 30px;
      // bottom: 20px;
      width: clamp(250px, 20vw, 340px);
      height: clamp(385px, 54vh, 440px);
      height: 90%;
      width: 84%;
      background: #ebf5fc;
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: 15px;
      display: flex;
      justify-content: center;
      // align-items: center;
      transition: 0.5s;

      .contest_content {
        padding: 20px;
        text-align: center;
        font-family: Montserrat;

        h2 {
          position: absolute;
          font-family: "Roboto";
          top: 60%;
          right: 32%;
          font-size: 8rem;
          color: rgba(0, 0, 0, 0.02);
          transition: 0.5s;
          pointer-events: none;
        }
        ul {
          margin: 0 10px;
        }
        h4 {
          font-size: 0.88rem;
          font-weight: 600;
          text-align: center;
          color: rgb(97, 97, 97);
          z-index: 1;
          transition: 0.5s;
        }
        li {
          font-size: 14px;
          font-weight: 300;
          text-align: left;
          color: #777;
          z-index: 1;
          transition: 0.5s;
          margin: 10px 0;
        }
        h3 {
          font-size: 1.2rem;
          z-index: 1;
          transition: 0.5s;
          display: inline-block;
          padding: 8px 10px;
          background: #03a9f4;
          border-radius: 10px;
          color: #fff;
          font-weight: 400;
          width: 100%;
          margin-bottom: 10px;
          // box-shadow: 0 10px 20px rgba(128, 128, 128, 0.308);
        }
        button,a {
          font-size: 1.2rem;
          z-index: 1;
          transition: 0.5s;
          display: inline-block;
          padding: 8px 10px;
          background: #ff568f;
          border-radius: 10px;
          color: #fff;
          font-weight: 400;
          width: fit-content;
          border: none;
          box-shadow: 0 10px 20px rgba(128, 128, 128, 0.308);
        }
      }
    }

    &:hover {
      .contest_box {
        transform: translateY(-50px);
        box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
        background: linear-gradient(45deg, #b95ce4, #4f29cd);
        .contest_content {
          h2 {
            color: rgba(0, 0, 0, 0.05);
          }

          h3 {
            color: white;
            background: #ff568f;
          }
          button,a {
            color: white;
            // background: #03a9f4;
            z-index: 11;
          }
          li,
          h4 {
            color: white;
          }
        }
      }
    }
  }
}



//mobiles

@media screen and (max-width: 620px) {
  .contest_container {
    .contest_title {
      font-size: 1.8rem;
    }
    .card_wrapper {
      padding-bottom: 100px;
    }

    .contest_card {
      .contest_box {
        .contest_content {
          h2 {
            top: 60%;
            right: 24%;
          }
          li {
            font-size: 12px;
          }
        }
      }
    }
  }
}

//mobile horizontal
@media screen and (max-height: 600px) {
  .contest_container {
    .contest_card {
      .contest_box {
        .contest_content {
          li {
            font-size: 12px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contest_title {
    margin-left: 3.6vw;
  }
}
