.rules {
  min-height: 100%;
  padding-bottom: 20%;
  display: flex;
  flex-direction: column;
  h1 {
    margin-left: 2vw;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 1.9rem;
    text-shadow: 0px 10px 7px rgba(128, 128, 128, 0.534);
    color: #0563fc;
    text-align: center;
  }
  li {
    font-family: Montserrat;
    font-style: normal;
    
    font-size: 0.75rem;
    margin: 10px 26px;
    text-align: justify;
    color: #002966;
  }

  .inner_rules {
    width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;

    div {
      margin: 20px;
      border-radius: 30px;
      width: clamp(250px, 70vw, 350px);
      min-height: clamp(400px, 60vh, 600px);
      border: 4px solid rgb(199, 199, 199);
      transition: all 0.3s ease;
    &:hover{
        border: 4px solid rgb(83, 129, 255);
        box-shadow: 0px 7px 20px 0px rgb(223, 220, 220);
        transform: scale(1.05);
        li{
            // font-weight: 600;
            // font-size: 0.8rem;
        }
    }
      h1 {
        margin-left: 0vw;
        margin-bottom: 1vw;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 800;
        font-size: 2.1rem;
        text-align: center;
        text-shadow: 0px 0px 0px;
        color: #0563fc;
      }
    }
  }
}

@media screen and (max-width: 620px) {
  .rules {
    h1 {
      font-size: 1.6rem;
    }
  }

  .inner_rules {
    flex-direction: column;
    padding-bottom: 150px;
    h1 {
      font-size: 1.6rem;
      margin-top:5px;
    }

  }
}





