$purple: #77009d;
$blue: #0066ff;
$ltblue: #15e0ff;
$magenta: #f8359e;
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@mixin font($font-family, $font-file) {
  @font-face {
    font-family: $font-family;
    src: url($font-file);
  }
}

@include font("Resolve-Medium", "./Assets/fonts/Resolve-Medium.otf");
@include font("Resolve-Semilight", "./Assets/fonts/Resolve-Semilight.otf");
@include font(
  "Resolve-SemiboldNrwIta",
  "./Assets/fonts/Resolve-SemiboldNrwIta.otf"
);
@include font(
  "Resolve-ExtraboldWdIta",
  "./Assets/fonts/Resolve-ExtraboldWdIta.otf"
);

// This is an example of animations & svgs... I completely understand that the amount of absolute positioning in this file is insane... ;) <3
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  a {
    color: white;
    outline: none;
    text-decoration: none;
  }
}

.body {
  background: rgb(0, 102, 255);
  background: linear-gradient(
    51deg,
    rgba(0, 102, 255, 1) 0%,
    rgba(0, 134, 255, 1) 33%,
    rgba(192, 0, 255, 1) 100%
  );
  height: 100vh;
  display: flex;
  align-items: center;
  // padding:2vh;
  justify-content: center;
}

.main {
  position: fixed;
  height: 90vh;
  width: 95vw;
  background: white;
  border-radius: 2vw;
  padding: 10px;
  overflow: hidden;
}

.navbar {
  position: sticky;
  padding: 4px;
  display: flex;
  background-color: white !important;
  justify-content: space-between;
  align-items: center;
  height: 15%;
  flex-wrap: wrap;
  z-index: 2000;

  .acm_logo {
    height: 80%;
  }
  .menu {
    font-size: 2.1rem;
    padding-right: 10px;

    // height:80%;
  }
  h2 {
    font-family: Resolve-Medium;
    font-style: normal;
    font-weight: 500;
    font-size: 2.5rem;
    color: #0061ff;

    font-family: Montserrat;
  }
  h4 {
    width: 100%;
    text-align: center;
    font-family: Resolve-Semilight;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 350;
    font-size: 1.25rem;
    letter-spacing: 0.57em;
    color: #0061ff;
  }
}

.page {
  height: 85%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.home {
  height: 100%;
  display: flex;
}

.about {
  height: 100%;
  display: flex;
  flex-direction: column;

  .about__heading {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 2.1rem;
    line-height: 2.688;
    padding: 0 14vw;

    color: #0563fc;
  }
  .about__image {
    padding: 0 4vw;
    width: 25%;
    position: relative;
    top: -4vw;
  }
  .about__column {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
  }

  .justi {
    text-align: justify;
  }
  .about_text {
    width: 50%;
    padding: 0 4vw;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 1rem;
    line-height: 1.35rem;
    text-justify: inter-word;
    color: #001168;
  }

  .about__text_wrapper {
    width: 100%;
    height: 100%;
    display: flex;
  }

  .about_text_heading {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #001168;
  }

  .about_name {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 700;
    font-size: 1rem !important;
    line-height: 1.25rem;
    color: #001168;
  }

  .about_designation {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 0.813rem !important;
    line-height: 1rem;
    /* identical to box height */
    color: #001168;
  }

  .about_row {
    width: 50%;
    height: 100%;
  }
}

.home_inner {
  max-height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  padding: 5vw 10vw 10vw 10vw;
}

.home_text_1 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 1.125rem;
  line-height: 1.375;
  color: #001168;
}
.home_text_2 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 3rem;
  line-height: 3.668rem;
  color: #0563fc;
  text-shadow: 0px 4px 7px rgba(0, 0, 0, 0.38);
}
.home_text_3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 3rem;
  line-height: 3.668rem;
  color: #001168;
}
.home_text_4 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 4rem;
  line-height: 4.875rem;
  letter-spacing: 0.19em;
  color: #001168;
}

.home_register {
  background: #0066ff;
  border-radius: 15px;
  padding: 0.625rem;
  font-size: 1.2rem;
  color: #ffffff;
  margin-top: 3.75rem;
  width: fit-content;
  border: none;
}

#fp-nav div {
  padding-right: 10px !important;
  color: #0563fc !important;
}

.register {
  height: max-content;
  width: 100%;
  display: flex;
  .reg_img_wrapper {
    width: 50%;
  }
  .reg_form {
    position: relative;
    top: -10px;
    width: 45%;
    height: max-content;
    padding: 1vw;
    background: #ffffff;
    box-shadow: 0px 0px 40px -15px rgba(0, 0, 0, 0.25);
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    padding: 1vw 4vw;
    form {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      // align-items: center;
      padding: 10px;
      overflow: auto;
    }
    input,
    select {
      height: 2.4rem;
      max-height: 32px;
      margin: 0px 0px 5px 0px;
      padding: 5px;
      border-radius: 10px;
      border: 1px #d1d1d1 solid;
      // outline: none !important;

      font-family: Montserrat;
    }

    .f_cont {
      max-width: 100%;
      display: flex;
      div {
        width: 50%;
      }
      input {
        width: 90%;
      }
    }

    label {
      display: block;
      color: #001168;
      font-family: Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 0.738rem;
      line-height: 1.125rem;
      color: #001168;
    }
  }
  .reg_heading {
    text-align: center;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5rem;
    line-height: 1.813rem;
    color: #0563fc;
  }
}

.hide_kardo {
  display: none;
  color: white !important;
}

@media screen and (max-width: 620px) {
  .body {
    align-items: flex-start;
    padding: 2vh;
  }
  // for mobile decvices

  .hide_kardo {
    display: block;
  }

  .register {
    position: relative;
    // padding-top: 18vh;
    // padding-bottom: 70px;
    // top: -18vh;
    flex-direction: column;
    align-items: center;
    height: max-content;

    .reg_heading {
      padding-top: 10px;
    }
    .reg_img_wrapper {
      // width: 90%;
      order: 2;
      display: none;
    }
    .reg_form {
      width: 88%;
      height: max-content;
      background: #ffffff;
      box-shadow: 20px 17px 50px -15px rgba(0, 0, 0, 0.25);
      border-radius: 40px;
      display: flex;
      flex-direction: column;
      padding: 1vw 5vw;
      overflow: auto;
    }
  }

  .about {
    .about__column {
      justify-content: center;
    }
    .about__image {
      width: 48%;
      position: relative;
      top: -10vw;
      padding-bottom: 4vh;
    }

    .about_designation {
      font-size: 0.713rem !important ;
      line-height: 1rem;
    }

    .about_name {
      /* Mr. Chandrashekhar Sahasrabudhe */

      font-size: 0.75rem !important;
      line-height: 1rem;
    }

    .about_text {
      width: 100%; /* ACM-W India,MUJ ACM Student Chapter is delighted to invite your participation in the 6th ACM India National Level Hackathon for Women in Computing. This hackathon aims at inspiring, recognizing and providing a platform to female students for enhancing their problem solving skills in Computing. Under one umbrella, female students from different parts of the nation would brainstorm, code and develop technologies that would solve societal problems and challenges. */
      font-size: 0.875rem;
      font-weight: 400;
    }

    .about__heading {
      text-align: center; /* ABOUT US */
      font-size: 1.5rem;
      line-height: 1.8rem;
      padding: 10px;
    }

    .about_text_heading {
      padding-top: 1vh;
      font-size: 0.813rem;
      line-height: 1rem;
      font-weight: 700;
    }
  }
  .home_text_2 {
    font-size: 2.5rem;
  }
  .main {
    padding: 0px;
    overflow: scroll;
    border-radius: 30px;
    height: 92vh;
  }
  .navbar {
    height: 10%;
    padding: 0.6vh 1.2vh;
    .acm_logo {
      height: 70%;
    }
    .menu {
      font-size: 1.9rem;
      
    }
  }
  .home {
    flex-direction: column;
    padding-top: 5vh;

    h1 {
      text-align: center;
      width: 100%;
    }
  }
  .home_inner {
    width: 100%;
    padding: 5px;
    padding-top: 10px;
  }
  .home_register {
    margin: 0 auto;
    margin-top: 0.875rem;
  }
  .home_image {
    transform: scale(0.8);
  }
}
.navbar {
  h2 {
    font-size: 2rem;
  }
  h4 {
    font-size: 1rem;
  }
}

//outlint fix in form

.register .reg_form input,
.register .reg_form select {
  outline: none !important;
}

@media screen and (max-height: 560px) {
  // when the device is in horizontal view
  .about {
    .about__image {
      width: 48%;
      position: relative;
      top: -10vw;
      padding-bottom: 28px;
    }
    .about_text {
      width: 50%;
      padding: 0 4vw;
      font-family: Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 0.75em;
      line-height: 1.05rem;
      color: #001168;
    }
    .about_designation {
      font-size: 0.613rem;
      line-height: 1rem;
    }
    .about_text_heading {
      font-size: 0.813rem;
      line-height: 0.8rem;
      font-weight: 700;
    }
    .about__heading {
      padding: 1vw 12vw !important;
      font-size: 1.5rem;
      line-height: 1.8rem;
      padding: 10px;
    }
    .about_name {
      /* Mr. Chandrashekhar Sahasrabudhe */
      font-size: 0.75rem;
      line-height: 1rem;
    }
  }
  .page {
    overflow: scroll;
  }
  .home_register {
    background: #0066ff;
    border-radius: 15px;
    padding: 0.625rem;
    font-size: 1.2rem;
    color: #ffffff;
    margin: 1.75rem;
    width: auto;
    border: none;
  }
  .home_text {
    padding-top: 3vw !important;
    padding-left: 5vw !important;
  }
  .home_text_1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 0.8rem;
    line-height: 1rem;
    color: #001168;
  }
  .home_text_2 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 2.2rem;
    line-height: 2rem;
    color: #0563fc;
    text-shadow: 0px 4px 7px rgba(0, 0, 0, 0.38);
  }
  .home_text_3 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 2rem;
    line-height: 2.4rem;
    color: #001168;
  }
  .home_text_4 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 2.6rem;
    line-height: 2.2rem;
    letter-spacing: 0.19em;
    color: #001168;
  }
  .home_inner {
    padding: 2vh;
  }
}
